* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #0A2647;
    color: #C69749;
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

#root {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 6rem;
}


.TodoCounter-title{
    font-size: 1.3rem;
    text-align: center;
    font-weight: 300;
    padding: 24px;
}
.todo-search {
  background: #f9fbfc;
  border-radius: 8px;
  border: 2px solid #205295;
  margin: 0 24px;
  height: 46px;
  width: calc(40% - 62px);
  min-width: 300px;
  font-size: 1.3rem;
  text-align: center;
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  outline: none;
}

.todo-search::-webkit-input-placeholder {
  color: #a5a5a5;
  font-weight: 300;
}

.todo-search::placeholder {
  color: #a5a5a5;
  font-weight: 300;
}



ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loading,
.create-first-todo,
.no-match {
  padding-top: 20px;
  text-align: center;
}

section {
  width: 500px;
}

