.custom-loader {
  margin-top: 50px;
  width:50px;
  height:24px;
  background: 
    radial-gradient(circle closest-side,#C69749 90%,#0000) 0%   50%,
    radial-gradient(circle closest-side,#C69749 90%,#0000) 50%  50%,
    radial-gradient(circle closest-side,#C69749 90%,#0000) 100% 50%;
  background-size:calc(100%/3) 10px;
  background-repeat: no-repeat;
  -webkit-animation:d3 1s infinite linear;
          animation:d3 1s infinite linear;
}
@-webkit-keyframes d3 {
    20%{background-position:0%   0%, 50%  50%,100%  50%}
    40%{background-position:0% 100%, 50%   0%,100%  50%}
    60%{background-position:0%  50%, 50% 100%,100%   0%}
    80%{background-position:0%  50%, 50%  50%,100% 100%}
}
@keyframes d3 {
    20%{background-position:0%   0%, 50%  50%,100%  50%}
    40%{background-position:0% 100%, 50%   0%,100%  50%}
    60%{background-position:0%  50%, 50% 100%,100%   0%}
    80%{background-position:0%  50%, 50%  50%,100% 100%}
}
.todo-item {
  position: relative;
  display: flex;
  border-radius: 50px;
  background-color: #144272;
  margin-top: 24px;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 5px 50px rgba(20, 66, 114, 0.5);
  width: 100%;
}

.todo-item-p {
    margin: 12px 0 12px 12px;
    width: calc(100% - 120px);
    font-size: 1.3rem;
    line-height: 24px;
}

.todo-item-p--complete{
    text-decoration: line-through;
}


.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}

.Icon-delete:hover {
    color: #282A3A;
}

.Icon-check--active {
    color: #4caf50;
}

.Icon-check:hover {
    color: #4caf50;
}


@media (max-width: 500px){
    .todo-item{
        max-width: 300px;
    }
}


.create-todo-button {
    background-color: #2C74B3;
    box-shadow: 0 5px 25px rgba(44, 116, 179, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    height: 64px;
    width: 64px;
    bottom: 24px;
    right: 24px;
    color: #C69749;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    z-index: 100;
}

.create-todo-button:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
}
header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  font-size: 2rem;
  padding-left: 0.5rem;
}

.main-title {
  height: 100%;
  font-size: 3rem;
}

@media (max-width: 400px) {
  header {
    flex-direction: column;
  }
  .main-title {
    text-align: center;
  }
}

.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 400px;
    background-color: #0A2647;
    padding: 33px 40px;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    color: #C69749;
    margin-bottom: 26px;
}

textarea{
    outline: none;
    background: #f9fbfc;
    border: 2px solid #205295;
    border-radius: 5px;
    font-size: 1.2rem;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    font-family: 'Poppins', sans-serif;
    color: #1e1e1f;
    resize: none;
}

textarea::-webkit-input-placeholder{
    color: #a5a5a5;
    font-weight: 300;
}

textarea::placeholder{
    color: #a5a5a5;
    font-weight: 300;
}

.todoForm-buttoncontainer{
    display: flex;
    justify-content: space-between;
    margin-top: 14px;
    width: 100%;
    align-items: center;
}

.todoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #C69749;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 8px;
    border: 2px solid #205295;

}

.todoForm-button--add{
    background-color: #2C74B3;
    box-shadow: 0px 5px 15px rgba(97, 218, 250, 0.3);
}

.todoForm-button--cancel {
    background: transparent;
}
