.todo-item {
  position: relative;
  display: flex;
  border-radius: 50px;
  background-color: #144272;
  margin-top: 24px;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 5px 50px rgba(20, 66, 114, 0.5);
  width: 100%;
}

.todo-item-p {
    margin: 12px 0 12px 12px;
    width: calc(100% - 120px);
    font-size: 1.3rem;
    line-height: 24px;
}

.todo-item-p--complete{
    text-decoration: line-through;
}


.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}

.Icon-delete:hover {
    color: #282A3A;
}

.Icon-check--active {
    color: #4caf50;
}

.Icon-check:hover {
    color: #4caf50;
}


@media (max-width: 500px){
    .todo-item{
        max-width: 300px;
    }
}

