.todo-search {
  background: #f9fbfc;
  border-radius: 8px;
  border: 2px solid #205295;
  margin: 0 24px;
  height: 46px;
  width: calc(40% - 62px);
  min-width: 300px;
  font-size: 1.3rem;
  text-align: center;
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  outline: none;
}

.todo-search::placeholder {
  color: #a5a5a5;
  font-weight: 300;
}


